.header-top {
    padding: 17px 0;
    box-shadow: 0 3px 15px rgba(157, 157, 157, 0.14);
}

.header-lk {
    padding: 3px 0;
    box-shadow: 0 1px 5px rgba(157, 157, 157, 0.14);

}

.logo_wrp {
    display: flex;
    justify-content: space-between;
    align-items: center;

}



.logo-top {
    display: flex;
    align-items: center;

}

.logo-top__img {
    flex-basis: 30%
}

.logo-top__title {
    font-size: 40px;
    font-weight: 700;
    color: $fontd;
    flex-basis: 70%;
    margin-left: 100px;
    margin-right: 10px;

}

.header-btn {
    height: 48px;
    font-size: 16px;
    color: #3a499d;
    background-color: #e7e7e7;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    font-size: 16px;
    line-height: 100%;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    padding: 0 1.9vw;
    transition: background .3s ease, color .3s ease, outline .3s ease, opacity .3s ease;
    cursor: pointer;
    height: 52px;
    white-space: nowrap;
    color: #3a499d;
    border: 0;

    &:hover {
        color: #273583;
        background-color: rgba(102, 120, 143, .3);
    }

}


.logo-top__img--lk {
    img {
        max-height: 38px;
    }
}

@media only screen and (max-width : 670px) {
    .logo-top__img {
        margin-right: 15px;
        max-width: 60px;
        min-width: 60px;
    }

    .logo-top__title {
        font-size: 18px;
        margin-left: 10px;
    }

}

.header-lk__btns {
    display: flex;
}

.btn-lk {
    display: flex;
    margin-right: 30px;
    align-items: center;
    font-size: 13px;
    opacity: .8;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        opacity: 1;
    }

}

.ic_lk {
    width: 20px;
    height: 20px;
    fill: #737D87;
    margin-right: 10px;
}

.ic-refresh {
    transform: rotate(230deg);
}
