.ui-slider {

    background: #5865ae;
    height: 5px;
    border: none;
    border-radius: 0 4px 4px 4px;
    width: 100%;
    box-shadow: none;
    cursor: pointer;
    margin-top: -4px;

}

.ui-slider-handle {
    border-radius: 50%;
    width: 19px !important;
    height: 19px !important;
    border: 5px solid #3a499d !important;
    margin-top: -2px;
    cursor: pointer !important;
    background: #fff !important;
    z-index: 90 !important;
}


.ui-slider-range {
    background: #dde1e6;

}

.ui-state-focus {
    outline: 0;
}

.pips {
    width: 100%;
    position: relative;
    margin-top: 9px;
    display: block;
    margin-bottom: 35px;
    z-index: 60;

    .pip {
        position: absolute;
        font: 14px Roboto, sans-serif;
        color: #66788f;
        white-space: nowrap;
        transform: translateX(-20px);

        &::before {
            content: "";
            position: absolute;
            top: -14px;
            width: 3px;
            height: 5px;
            background: #a1acba;
            left: 50%;
        }
    }
}

.pip1 {
    left: 30px;

    &::before {
        display: none;
    }

}

.pip2 {
    left: 20%;
}

.pip3 {
    left: 40%;
}

.pip4 {
    left: 60%;
}

.pip5 {
    left: 80%;
}

.pip6 {
    left: 96%;

    &::before {
        display: none;
    }
}

.pip1-1 {
    left: 30px;

    &::before {
        display: none;
    }


}

.pip2-2 {
    left: 25%;
}

.pip3-3 {
    left: 50%;
}

.pip4-4 {
    left: 75%;
}

.pip5-5 {
    left: 96%;

    &::before {
        display: none;
    }
}

@media only screen and (max-width : 576px) {
    .pip2,
    .pip3,
    .pip4,
    .pip5,
    .pip2-2,
    .pip3-3,
    .pip4-4,

        {
        display: none
    }

    .pip6 {
        left: 90%;

    }

}
