.table thead th {
    position: relative;
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6 !important;
}

.table > :not(:last-child) > :last-child > * {
    border-bottom-color: transparent;
}

.section_lk {
    background: #F5FAFF;
    min-height: 100vh;
    position: relative;

    table {
        overflow: hidden;
        border-radius: 4px !important;
        z-index: 90;
        position: relative;
    }

    .table--lk {
        margin-top: 20px;
    }

    .table--lk {
        background: #fff;
    }

    .table--lk > tbody {
        background: #fff;

    }


    thead th {
        font-size: 14px;
        color: #868788;
        padding: 14px 15px !important;
        line-height: 1.3 !important;
        vertical-align: top;


    }

    .table > tbody {
        font-size: 13px;
        background: #fff;
        line-height: 1.3;

        td {
            padding: 14px 15px;
        }
    }

    .td-date {
        font-size: 13px;

    }
}


.active-top {


    &::after {
        right: 1em;
        content: "\f0de";
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        font-size: 1rem;
        opacity: 1;
        position: absolute;
        bottom: 1.3em;
        display: block;
        opacity: .3;
        cursor: pointer;
    }

    &::before {
        content: "\f0dd";
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        right: 16px;
        font-size: 1rem;
        position: absolute;
        bottom: 1.3em;
        display: block;
        cursor: pointer;
    }
}

.active-bottom {


    &::after {
        right: 1em;
        content: "\f0de";
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        font-size: 1rem;
        opacity: 1;
        position: absolute;
        bottom: 1.3em;
        display: block;

        cursor: pointer;
    }

    &::before {
        content: "\f0dd";
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 900;
        right: 16px;
        font-size: 1rem;
        position: absolute;
        bottom: 1.3em;
        display: block;
        cursor: pointer;
        opacity: .3;
    }
}

.bg_img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 10;

    img {
        display: block;
    }
}


.load-more {
    padding: 7px;
    background: #A3AEBC;
    margin: 0 auto;
    text-align: center;
    cursor: pointer;
    font-size: 16px;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 170px;
    color: $fontd;

    &:hover {
        color: $fontd;
        background: darken(#A3AEBC, 5%);
    }
}
