.section-enter {
    background: #F5FAFF;
    min-height: 100%;
    position: relative;
}

.enter-form {
    background: #fff;
    box-shadow: 0 5px 40px 0 rgb(2 24 150 / 12%);
    width: 758px;
    padding: 20px 30px 30px 30px;
    position: relative;
    z-index: 20;

    .input_title {
        text-align: center;
    }



}

.btn-next {
    padding: 23px;
    width: 100%;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #A3AEBC;
    font-weight: 600;
    border-radius: 10px;
    font-size: 19px;
    max-width: 432px;
    margin: 0 auto;
    text-align: center;
    cursor: pointer;

    &:hover {
        color: #fff;
        background: darken(#A3AEBC, 5%);
    }
}

.btn-next--blocked {
    cursor: not-allowed;
    opacity: .5;

    &:hover {
        color: #fff;
        background: darken(#A3AEBC, .1%);
    }
}

.bg_img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 10;

    img {
        display: block;
    }
}

@media only screen and (max-width : 576px) {
    .enter-form {
        width: auto;
        padding: 20px 15px 30px 15px;

    }

}
