.cresults {
    &__result {
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;
    }

    &__labels {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 10px;
    }

    &__label--title {
        max-width: 115px;
        color: #001e45;
        display: block;
        font: 500 16px Roboto, sans-serif;
        line-height: 1.5;

    }

    &__label--summ {
        color: #001e45;
        display: block;
        font: 700 36px Roboto, sans-serif;
        white-space: nowrap;
        line-height: 1
    }

    &__progress {
        position: relative;
        height: 4px;
        width: 100%;
        display: block;
    }

    &__progress--gray {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        border-radius: 3px;
        width: 100%;
        background: #dde1e6;
    }

    &__progress--red {
        position: absolute;
        left: 0;
        top: 0;
        height: 100%;
        border-radius: 3px;
        background: #cc003d;
    }

    &__checkbox {
        margin-bottom: 40px;
    }

    &__link {
        font-size: 18px;
        color: $fontd;
        margin-left: 30px;
        display: block;
        margin-bottom: 40px;
        font-weight: 500;

        &:hover {
            color: $red;
        }
    }

    &__btn {
        padding: 23px;
        width: 100%;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $red;
        text-transform: uppercase;
        font-weight: 600;
        border-radius: 3px;
        font-size: 19px;

        &:hover {
            color: #fff;
            background: darken($red, 5%);
        }
    }
}

@media only screen and (max-width : 576px) {
    .cresults {
        &__label--summ {
            font: 700 26px Roboto, sans-serif;

        }

        &__link {
            font-size: 14px;

        }

    }



}

.cresults__btn--disabled {
    opacity: .5;
    cursor: not-allowed;

    &:hover {
        background: darken($red, .1%)
    }
}
