.section {
    position: relative;
    display: block;
    padding: 32px 0;
}

.product-form {
    padding: 20px 30px 30px 30px;
    border-radius: 16px;
    box-shadow: 0 4px 22px rgba(0, 0, 0, .14);
    margin-right: 25px;
}

.input_title {
    font: 700 30px Roboto, sans-serif;
    color: #001e45;
    margin-bottom: 20px;
}

.double-input {
    display: flex;
    position: relative;


}

.rouble {
    position: absolute;
    right: 30px;
    top: 22px;
    color: #66788F;
    font-size: 22px;
}

.input-wrp {
    margin-bottom: 7px;
    position: relative;
}

.input-wrp--slider {
    margin-bottom: 50px;
}

.calc-input {
    padding: 8px 16px;
    padding-top: 8px;
    font: 23px Roboto, sans-serif;
    line-height: normal;
    color: #001e45;
    border: 1px solid #dde1e6;
    background: #f2f2f7;
    border-radius: 10px;
    width: 100%;
    height: 65px;
    transition: color .3s, background .3s, border-color .3s;
    line-height: 144%;
    -webkit-user-select: text;
    margin-bottom: 30px;


}

.bordered-right {
    width: 79%;
    border-right: none;
    border-radius: 10px 0 0 10px;
    margin-bottom: 0;
}

.bordered-left {
    width: 21%;
    border-left: none;
    border-radius: 0 10px 10px 0;
    margin-bottom: 0;
}

.credit-term {
    left: 16px;
    top: 10px;
    pointer-events: none;
    position: absolute;
    font: 14px Roboto, sans-serif;
    line-height: normal;
    color: #66788f;
    transition: top .3s, left .3s, transform .3s, font-size .3s;
    line-height: 150%;
}

.input-mounth {
    padding-top: 30px;
    margin-bottom: 0;
    font-size: 18px;
}


@media only screen and (max-width : 992px) {
    .product-form {
        margin-right: 0px;
        margin-bottom: 20px
    }
}

@media only screen and (max-width : 576px) {
    .product-form {
        padding: 0;

        box-shadow: none;
    }

    .input_title {
        font: 700 22px Roboto, sans-serif;
        color: #001e45;
        margin-bottom: 18px;
    }

    .calc-input {

        font: 18px Roboto, sans-serif;

    }



}


.input-err {
    border: 1px solid $red;
    color: $red;
    background: rgba(237, 0, 0, 0.05);
    margin-bottom: 5px;

}


.input-mounth {
    margin-bottom: 0;
}


.err-msg {
    position: relative;
    display: block;
    color: $red;
    border-radius: 4px;
    font-size: 14px;
    padding: 7px;
    bottom: 0;
    margin-bottom: 5px;
}

.ic_err {
    position: absolute;
    right: 15px;
    top: 22px;
    width: 24px;
    height: 24px;
    fill: $red;
}


.disabled {
    opacity: .5;
}
