    .custom-checkbox {
        display: flex;
        width: 100%;
        margin: 0;
        cursor: pointer;

    }

    .checkbox-hidden {
        display: none;
    }

    .checkbox-wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 25px;
        max-width: 25px;
        flex: 0 0 25px;
        height: 25px;
        border-radius: 2px;

        background: $red;
    }

    .custom-checkbox input:checked + .checkbox-wrap::after {
        transform: scale(1);
        opacity: 1;
    }

    .custom-checkbox .checkbox-wrap::after {
        content: "";
        display: block;
        width: 12px;
        height: 10px;
        background: url(../img/svg-check-ic.svg) no-repeat 0 0;
        will-change: transform;
        transform: scale(0);
        opacity: 0;
        transition: all 0.1s linear;

    }

    .checkbox-text {
        margin-left: 10px;
        color: $fontd;
        line-height: 1.1;
        margin-top: -3px;
        font-size: 18px;
        width: 80%;
        font-weight: 500;
    }

    @media only screen and (max-width : 576px) {
        .checkbox-text {

            font-size: 14px;

        }
    }
